const Contents = () => {
  return <></>;
};

const ContentDescrip = styled.div`
  padding: 0 30px 0 30px;

  
`;

const IntroContainer = styled.div`
  margin: 50px 0 50px 0;
  .introtext_div {
    padding: 0 30px 0 30px;
    @media screen and (max-width: 1000px){
    padding: 0 10px 0 10px;
  }

    .parag_div {
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
      
    }

    .parag_imagediv {
      display: flex;
      align-items: center;

      @media screen and (max-width: 1000px) {
        flex-direction: column;
      }

      img {
        width: 50%;
        @media screen and (max-width: 1000px){
          width: 100%;
        }
      }

      p {
        padding: 0 10px 0 10px;
        text-align: center;
      }
    }

    .parag_1 {
      font-size: 24px;
      font-weight: 300;
      color: #6d6d6d;
      margin-bottom: 30px;
      .parag1_span {
        color: red;
        font-style: italic;
        font-weight: 400;
      }
    }

    .parag_2 {
      display: flex;
      align-items: center;
      justify-content: center;

      font-size: 20px;
      font-weight: 500;
      line-height: 3;
      color: black;

      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    
        @media screen and (max-width: 1000px){
          line-height: 1.50;
          text-align: center;
          padding: 5px 0 5px 0;
        }
      }

    .parag_3 {
      font-size: 20px;
      font-weight: 400;
      line-height: 1.7;
      color: black;
      /* text-transform: capitalize; */
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    }
  }
  .intro_shortmenu {
    /* padding: 0 30px 0 30px; */
    margin-top: 25px;
    margin-left: 30px;
    margin-right: 30px;

    @media screen and (max-width: 1000px) {
      margin: 25px 10px 0 10px;
    }

    .privatelabel_div {
      margin-top: 50px;
      margin-bottom: 20px;

      .sections {
        margin: 30px 0 50px 0;
        padding: 20px 0 20px 0;
        display: flex;
        justify-content: space-evenly;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        background-color: #bada55;

        @media screen and (max-width: 1000px){
          display: grid;
          grid-template-columns: 50% 50%;
          
          
        }
        .items {
          background-color: #bada55;
          width: 150px;
          height: 150px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin: 10px 0 10px 0;
          border-radius: 20px;
          box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
          transition: 0.3s ease;

          @media screen and (max-width: 1000px){
            width: auto;
            margin: 10px 10px;
          }

          &:hover {
            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
          }

          p {
            color: white;
            font-weight: 500;
            font-size: 20px;
            text-align: center;
          }
          h3 {
            font-size: 25px;
          }
        }
      }
    }

    ul {
      margin-top: 10px;
      li {
        background-color: #ccf4ff;
        width: fit-content;
        margin-top: 5px;
        margin-left: 20px;
      }
    }
  }
`;
const TableContainer = styled.div`
  width: 100%;
  background-color: #1e7de0;
  margin-top: 50px;
  margin-bottom: 100px;

  @media screen and (max-width: 1000px){
    margin: 50px 0;
  }

  .grid_headercontainer {
    display: flex;
    align-items: center;
    justify-content: center;

    

    .grid_headertext {
      color: white;
      text-transform: uppercase;
      margin-top: 50px;

      @media screen and (max-width: 1000px){
        text-align: center;
        margin: 25px 0;
      }
    }
  }

  .grid_container {
    padding: 50px;
    display: grid;
    grid-template-columns: 25% 25% 25% 25%;

    @media screen and (max-width: 1000px){
      padding: 5px;
      grid-template-columns: 50% 50%;
    }

    .grid_items {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 250px;
      border: 0.5px solid white;
      color: white;
    }
  }
`;

const PageContainer = styled.div`
  display: flex;
  flex-direction: row;
  position: relative;
  height: 100vh;
  

  .fason_sidebar{
    display: block;
    
    

    @media screen and (max-width: 1000px){
      display: none;
    }
  }

  .fason_mobilesidebar{
    display: none;

    @media screen and (max-width: 1000px){
      display: block;
    }
  }
`;

const ContentWrapper = styled.div`
  display: block;
  position: relative;
  width: 100%;
  background-color: white;
  border-radius: 3px;
  overflow: auto;
  margin-left: 30px;
`;

const HeaderContainer = styled.div`
  width: 100%;
  height: 300px;
  display: flex;

  img{
    width: 100%;

    @media screen and (max-width: 1000px) {
      position: relative;
      width: 100%;
      height: auto;
      opacity: 1;
    }
  }

  h1 {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    font-size: 40px;

    @media screen and (max-width: 1000px){
      display: block;
      
      position: absolute;
      /* top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); */

      top: 100px;

      font-size: 25px;
      line-height: 1;

      text-align: center;

      width: 100%;

      color: black;
      

      p {
        margin-top: 10px;
        font-size: 12px;
      }
    
  }

    p {
      font-size: small;
      color: black;
    }

    
  }
`;
